@import url(https://fonts.googleapis.com/css?family=Abril+Fatface|Merriweather+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Abril+Fatface|Merriweather+Sans&display=swap);
body,
span,
div,
html,
h1,
h2,
h3,
ul,
li,
p,
a,
section,
nav,
header,
i,
article {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border: none;
}

body {
  overflow-x: hidden;
  font-family: 'Merriweather Sans', sans-serif;
}

ul,
li {
  list-style: none;
}
a:link,
a:visited,
a:hover {
  text-decoration: none;
  color: #333;
}
a:active,
a:focus {
  text-decoration: none;
  color: #333;
}

nav,
section,
article {
  display: block;
}

img {
  border: none;
}

#skipmenu a {
  background-color: #cce1e8;
  display: block;
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: #555;
  font-size: 18px;
  position: absolute;
  top: -9999px;
}

#skipmenu a:hover,
#skipmenu a:active,
#skipmenu a:focus {
  top: 0;
}

.cf:after {
  content: '';
  clear: both;
  display: block;
}

.flt_left {
  float: left;
}
.flt_right {
  float: right;
}

._skip {
  text-indent: -999px;
  width: 1px;
  height: 1px;
  overflow: hidden;
  font-size: 0px;
  position: absolute;
  top: -99px;
  left: -99px;
}

.smallAdImageWrap {
  border: 1px solid blueviolet;
  width: 560px;
}

.leftContents {
  width: 787px;
  height: 100%;
  position: relative;
}

.lft_div {
  position: absolute;
  bottom: 0;
  left: 0;
}

.leftContents article {
  float: left;
}
.leftContents article:first-child a {
  display: block;
  height: 101%;
  color: #fff;
  line-height: 103px;
  text-align: center;
}
.leftContents article:first-child > div {
  width: 207px;
  height: 207px;
}

.leftContents article > div:first-child {
  -webkit-transition: 0.5s ease-in;
  transition: 0.5s ease-in;
  background-color: #80a29c;
}

.leftContents article > div:last-child {
  -webkit-transition: 0.5s ease-in;
  transition: 0.5s ease-in;
  background-color: #4c675c;
}

.ractBox {
  width: 103px;
  height: 103px;
}

.hovBtn {
  position: relative;
}

.button_hover {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background: -webkit-gradient(
    linear,
    left bottom, left top,
    from(rgba(0, 0, 0, 0.2)),
    color-stop(70.71%, rgba(0, 0, 0, 0))
  );
  background: linear-gradient(
    0deg,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0) 70.71%
  );
  -webkit-transition: all 0.1s ease-in;
  transition: all 0.1s ease-in;
  opacity: 0;
}
.hovBtn:hover .button_hover {
  opacity: 1;
}

.membership {
  width: 103px;
  height: 206px;
  padding-top: 103px;
}

.Acc {
  font-size: 30px;
  line-height: 103px;
}

.Cart {
  -webkit-transition: 0.5s ease-in;
  transition: 0.5s ease-in;
  background: url(data:image/png;base64,) no-repeat 50% 50%;
  background-color: #a3c4be;
}

.Buybtn {
  -webkit-transition: 0.5s ease-in;
  transition: 0.5s ease-in;
  background-color: #283f36;
}

.viewMore {
  width: 100%;
  height: 103px;
}

.leftContents article:last-child {
  width: 580px;
  /* border: 1px solid red; */
}

.explnationWrap {
  padding: 65px 0 0 82px;
}

.explnation {
  display: block;
  position: relative;
  font-size: 10px;
  width: 233px;
  height: 90px;
}

.explnation span {
  position: absolute;
  bottom: 0;
  left: 0;
}

h3.productName {
  font-family: 'Abril Fatface', cursive;
  font-size: 58px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: hidden;
  padding-left: 82px;
}
.productName > p {
  width: 500px;
  -webkit-transition: 1s ease;
  transition: 1s ease;
}
.productName > p > span {
  display: block;
}
.rightInfo {
  width: 200px;
  height: 100%;
  margin-right: 91px;
}

/* right info */
.currentPage {
  width: 100%;
  height: 629px;
  position: relative;
}

.currentPage > ul {
  width: 15px;
  float: right;
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -5px;
}

.currentPage > ul > li {
  -webkit-transition: 0.1s ease-in;
  transition: 0.1s ease-in;
  width: 15px;
  height: 15px;
  float: left;
  border: 1px solid #a3c4be;
  font-size: 0;
  margin-bottom: 12px;
  cursor: pointer;
}

.currentPage > ul > li.on {
  border: 1px solid #3c571d;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.socialM {
  margin-bottom: 65px;
}

.socialM ul {
  float: right;
}
.socialM ul li {
  width: 27px;
  height: 27px;
  float: left;
  margin-left: 27px;
}

.socialM ul li:first-child {
  background: url(data:image/png;base64,);
}
.socialM ul li:last-child {
  background: url(data:image/png;base64,);
}

.socialM ul li a {
  display: block;
  height: 100%;
}

.backgroundWrapper {
  height: 100%;
  width: 100%;
  position: fixed;
  /* position: absolute; */
  top: 0;
  left: 0;
}

.backgroundColor {
  width: 100%;
  height: 100%;
  position: relative;
}

.backgroundColor > li {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -10;
}

.backgroundColor > li.on {
  z-index: -8;
}

.back_division {
  position: relative;
  width: 100%;
  height: 100%;
}

.beforeBack {
  width: 40%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.backgroundColor > li.on > div > i {
  background-color: #cce1e8;
}

.backgroundColor > li > div > i {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 40%;
  height: 100%;
  float: left;
  
}

.backgroundColor > li > div > span {
  position: relative;
  display: block;
  width: 60%;
  height: 100%;
  float: right;
  -webkit-transition: 4s ease;
  transition: 4s ease;
}

.backgroundColor > li {
  -webkit-transition-delay: 0.2s;
          transition-delay: 0.2s;
  -webkit-transition: 2s ease;
  transition: 2s ease;
  background-color: transparent;
}

.backgroundColor span {
  opacity: 0;
}
.back0 span {
  opacity: 1;
}

.backgroundColor > li > div > span:after {
  position: absolute;
  top: 0;
  right: 0;
  content: '';
  width: 100%;
  height: 100%;
  background-size: cover;
}

.back0 span:after {
  background: url(/b-mercy-juice/static/media/bck_img1.0f31309d.jpg) no-repeat 15vw -15vh fixed;
}
.back1 span:after {
  background: url(/b-mercy-juice/static/media/bck_img2.ad34c9f1.jpg) no-repeat 15vw -15vh fixed;
}
.back2 span:after {
  background: url(/b-mercy-juice/static/media/bck_img3.667440e1.jpg) no-repeat -20vw 0 / 100% 0;
}

#wrapper {
  width: 100vw;
  overflow-x: hidden;
  position: relative;
}

#container {
  /* width: 1366px; */
  width: 85vw;
  height: 768px;
  margin: 55px auto;
  position: relative;
  padding-top: 45px;
}

.sect {
  width: 100%; /* maxsize of responsive */
  height: 100%;
}

/* Header */
.header {
  width: 100%;
  height: 45px;
  position: absolute;
  top: 0;
  left: 0;
  padding: 0 91px;
}

.logo {
  width: 142px;
  height: 70px;
  position: relative;
}

.logo:after {
  content: '';
  position: absolute;
  top: 0;
  right: -80px;
  width: 0px;
  height: 0px;
}

.navMenu {
  min-width: 585px;
  height: 45px;
}
.navMenu > li {
  float: left;
  width: 20%;
  height: 100%;
  line-height: 45px;
  text-align: right;
}

.navMenu > li > a {
  color: #212d2f;
  font-size: 20px;
  font-family: 'Abril Fatface', cursive;
}

.SectTit {
  width: 100%;
  height: 60px;
}

